.shape.canBePlayed {
  cursor: pointer;
}

.shape.canBePlayed:hover {
  background-color: #f0f0f0;
  -webkit-box-shadow: 0 2px 3px rgba(10, 10, 10, 0.4), 0 0 0 1px rgba(10, 10, 10, .4);
          box-shadow: 0 2px 3px rgba(10, 10, 10, 0.4), 0 0 0 1px rgba(10, 10, 10, .4);
}

.shape.selected {
  background-color: #ffdd57 !important;
  -webkit-box-shadow: none;
          box-shadow: none;
}

.shape.canBePlayed.selected:hover {
  -webkit-box-shadow: 0 2px 3px rgba(10, 10, 10, 0.4), 0 0 0 1px rgba(10, 10, 10, .4);
          box-shadow: 0 2px 3px rgba(10, 10, 10, 0.4), 0 0 0 1px rgba(10, 10, 10, .4);
}


